<template>
  <div class="the-welcome-dmex">
    <div class="the-welcome-dmex__section-wrapper">
      <div class="the-welcome-dmex__headline-wrapper">
        <div class="the-welcome-dmex__headline">Welcome to <span class="highlight">D-MEX</span>!</div>
        <div class="the-welcome-dmex__headline-author">Intro from the Founder, Max Nicula</div>
        <div class="the-welcome-dmex__description">Max started his entrepreneurial career at the age of 14 while still in high school. A graduate of The London School of Economics (LSE) with a degree in Operational Research, in his lifetime accumulated deep experience in multiple fields among which coding, website development, automation, microelectronics and high-frequency trading software development.</div>
      </div>

      <div class="the-welcome-dmex__content-wrapper">
        <div class="the-welcome-dmex__content">
          <p class="the-welcome-dmex__content-paragraph">Started interacting with Bitcoin in 2013 and entered the crypto market in 2015 with a Bitcoin sales website. In 2016 he took over the defunct Romanian exchange <b>BTCxCHANGE</b>, relaunched it and made it the nr.1 Bitcoin exchange in Romania. After multiple bank account closures decided to stop dealing with banks and went into the decentralized exchange market, launching <a href="http://ethermium.com" target="_blank">EtherMium.com</a> in summer 2018 (the best ERC20 token exchange on the market by far).</p>
          <p class="the-welcome-dmex__content-paragraph">In 2019, DMEX is launched. The first decentralized margin trading exchange, aiming to bring the new era of trading to the masses. As it should be. Decentralized, permissionless and anonymous.</p>

          <div class="the-welcome-dmex__content-projects">
            <span class="the-welcome-dmex__content-project-item">
              <e-icon :src="iconBtcExchangeLogo" />
            </span>
            <a class="the-welcome-dmex__content-project-item" href="http://ethermium.com" target="_blank">
              <e-icon :src="iconEthermiumLogo" />
            </a>
            <a class="the-welcome-dmex__content-project-item dmex" href="http://dmex.app" target="_blank">
              <e-icon :src="iconDmexLogo" />
            </a>
          </div>
        </div>

        <div class="the-welcome-dmex__video" @click="isVideoWelcomePopup = true">
          <img src="/static/img/landing-video-preview.jpg" alt="" class="the-welcome-dmex__video-preview">
        </div>
      </div>
    </div>

    <the-welcome-video-popup :close="close" v-if="isVideoWelcomePopup" />
  </div>
</template>

<script>
import TheWelcomeVideoPopup from '@/containers/blocks/TheWelcomeVideoPopup';
import EIcon from '@/components/EIcon';

import iconBtcExchangeLogo from '@/icons/e-icon__btcexchange.svg';
import iconEthermiumLogo from '@/icons/e-icon__ethermium.svg';
import iconDmexLogo from '@/icons/e-icon__dmex.svg';

export default {
  name: 'the-welcome-dmex',

  data() {
    return {
      isVideoWelcomePopup: false,

      // Icons
      iconBtcExchangeLogo,
      iconEthermiumLogo,
      iconDmexLogo,
    };
  },

  methods: {
    close() {
      this.isVideoWelcomePopup = false;
    },
  },

  components: {
    TheWelcomeVideoPopup,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-welcome-dmex {
  $this: &;

  position: relative;
  background: #ECECEF;
  border-radius: 40px 40px 0 0;
  padding: 120px 0 180px;

  @include r(992) {
    padding: 60px 0;
  }

  @include r(767) {
    padding: 40px 0 60px;
  }

  &__section-wrapper {
    width: 100%;
    max-width: 1280px;
    padding: 0 30px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;

    @include r(767) {
      padding: 0 15px;
    }
  }

  &__headline-wrapper {
    width: 100%;
    max-width: 680px;

    @include r(992) {
      text-align: center;
      margin: 0 auto;
    }
  }

  &__headline {
    font-weight: 500;
    font-size: 36px;
    color: #112271;
    letter-spacing: 0;
    line-height: 42px;
    margin-bottom: 12px;

    @include r(767) {
      font-size: 30px;
      line-height: 36px;
    }
  }

  &__headline-author {
    font-weight: 500;
    font-size: 16px;
    color: #6B7397;
    letter-spacing: 1px;
    line-height: 25px;
    text-transform: uppercase;
    margin-bottom: 12px;

    @include r(767) {
      font-size: 15px;
      line-height: 22px;
    }
  }

  &__description {
    font-weight: 500;
    font-size: 18px;
    color: #112271;
    letter-spacing: 0;
    line-height: 25px;

    @include r(767) {
      font-size: 16px;
      line-height: 22px;
    }
  }

  .highlight {
    color: #00DB9C;
  }

  &__content-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;

    @include r(992) {
      flex-direction: column;
      justify-content: flex-end;
      text-align: center;
    }
  }

  &__content {
    flex: 0 0 410px;
    margin-right: 40px;

    @include r(992) {
      flex: auto;
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__content-paragraph {
    font-weight: 500;
    font-size: 15px;
    color: #6B7397;
    letter-spacing: 0;
    line-height: 23px;
    margin: 0 0 15px;

    @include r(767) {
      font-size: 15px;
      line-height: 20px;
    }

    a, b {
      font-weight: 500;
      color: #112271;
      text-decoration: none;
      transition: opacity .15s ease-in-out;
    }

    a:hover {
      opacity: .8;
    }
  }

  &__video {
    flex: 1;
    max-width: 700px;

    @include r(992) {
      width: 100%;
      max-width: none;
      text-align: center;
    }
  }

  &__video-preview {
    max-width: 700px;
    width: 100%;
    max-height: 100%;
    border-radius: 18px;
    cursor: pointer;
    padding: 5px;
    box-sizing: border-box;
    background: #fff;
    overflow: hidden;
    box-shadow: 0 18px 78px 0 rgba(12,41,109,0.30);
    transition: all .15s ease-in-out;

    &:hover {
      opacity: .95;
      transform: translate(0, -1px);
    }
  }

  .e-modal__body {
    width: 100%;
    max-width: 640px;
  }

  .e-modal__inner {
    line-height: 0;
  }

  &__content-projects {
    display: flex;
    padding-top: 30px;

    @include r(992) {
      justify-content: center;
    }
  }

  &__content-project-item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 130px;
    height: 72px;
    margin-right: 15px;
    background: #040B48;
    border-radius: 15px;
    padding: 12px 15px;
    box-sizing: border-box;
    transition: opacity .15s ease-in-out;

    &:last-child {
      margin-right: 0;
    }

    &:hover {
      opacity: .87;
    }

    .e-icon {
      width: 100%;

      svg {
        top: 50%;
        transform: translate(0, -50%);
      }
    }

    &.dmex {
      .e-icon {
        max-width: 58px;
      }
    }
  }
}
</style>



