<template>
  <transition-modal>
    <e-modal
      @close="close"
      :btnClose="false"
    >
      <template slot="inner">
        <youtube
          video-id="6bgs1NDdfts"
          @ready="youtubePlayerReady"
          @playing="onVideoPlaying"
          player-width="100%">
        </youtube>

        <button class="the-welcome-video-popup__close" @click="close">Close</button>
      </template>
    </e-modal>
  </transition-modal>
</template>

<script>
/**
 * Component: Всплывающее окно с Welcome to DMEX
 *
 */

import TransitionModal from '@/containers/transitions/TransitionModal';
import EModal from '@/components/EModal';

export default {
  name: 'the-welcome-video-popup',

  props: {
    close: {
      type: Function,
    },
  },

  data() {
    return {
      isVideoPlaying: false,
    };
  },

  methods: {
    youtubePlayerReady(event) {
      this.player = event.target;

      this.youtubePlayVideo();
    },

    youtubePlayVideo() {
      this.player.playVideo();
      this.isVideoPlaying = true;
    },

    onVideoPlaying() {
      this.isVideoPlaying = true;
    },
  },

  components: {
    TransitionModal,
    EModal,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-welcome-video-popup {
  $this: &;

  &__close {
    position: absolute;
    bottom: -50px;
    left: 50%;
    transform: translate(-50%, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 85px;
    height: 34px;
    background-image: linear-gradient(-180deg, #C66D6D 0%, #DC4727 100%);
    box-shadow: inset 0 1px 2px 0 #FB7D5D;
    font-weight: 500;
    font-size: 14px;
    color: #FFFFFF;
    text-align: center;
    line-height: 25px;
    text-decoration: none;
    text-shadow: 0 1px 1px #832703;
    border-radius: 5px;
    overflow: hidden;

    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      right: 0;
      opacity: 0;
      background: #fff;
      transition: opacity .15s ease-in-out;
    }

    &:hover {
      &:before {
        opacity: .1;
      }
    }

    &:active {
      transform: translate(-50%, 1px);
    }
  }
}
</style>



